<!--全部、来信、来访、网信切换-->
<template>
  <ul class="typeChange">
    <li
      v-for="(item, index) in arr"
      :key="index"
      @click="changeType(item)"
      :class="curType === item.name ? 'active' : ''"
    >{{item.name}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        arr: [
          {
            "name": "全部",
            "id": "drop"
          },
          {
            "name": "来信",
            "id": "来信"
          },
          {
            "name": "来访",
            "id": "来访"
          },
          {
            "name": "网信",
            "id": "网信"
          }
        ],
        curType: "全部",
        curTypeId: "drop",
      }
    },
    // mounted(){
    //   this.$emit("change", this.curTypeId);
    // },
    methods: {
      changeType(item) {
        this.curType = item.name;
        this.curTypeId = item.id;
        this.$emit("change", this.curTypeId);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .typeChange
    position absolute
    width 7.4rem
    height 0.5rem
    top -0.45rem
    left 5.4rem
    z-index 1000
    li
      float left
      color #8df6ff
      line-height 0.5rem
      font-size 0.2rem
      margin 0 0.2rem
      cursor pointer
      &.active
        color #fff
        font-weight 700
        text-shadow 0 0 5px #0cafc9
</style>
