<template>
  <div class="wrapper">
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-top">
        <div class="ct-left clearfloat">
          <ctitle :title="'信访量信访目的占比'"></ctitle>
          <div class="inner">
            <pie-chart3D :params="zbPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="ct-right">
          <ctitle :title="'信访量信访目的走势'"></ctitle>
          <div class="inner">
            <timeChange class="timeChange" :key="curTypeId" @change="zsTimeClick"></timeChange>
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <ctitle :title="'信访量四级登记机构-信访目的占比'"></ctitle>
        <div class="inner">
          <div class="chart" v-for="(item,index) of djjgArr" :key="index">
            <loopPie :params="item.params"></loopPie>
            <p class="label">{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import timeChange from "@/components/timeChange/index";
  import incNumber from "@/components/incNumber/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";

  export default {
    name: "xfxs",
    components: {
      ctitle,
      typeChange,
      timeChange,
      incNumber,
      loopPie,
      PieChart3D,
      LinesR,
    },
    data() {
      return {
        loadFlag: false,
        curTypeId: 'drop',
        // 信访目的占比
        zbPieParams: {
          data: [],
          color: this.$STANDARD.pieColors,
          angle: 20,
          radius: "35%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        // 信访目的走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber:4,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName:"件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "8%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "8%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
        //登记机构占比
        djjgArr: [
          {name: '国家', params: {data: [],options:{}}},
          {name: '省级', params: {data: [],options:{}}},
          {name: '市级', params: {data: [],options:{}}},
          {name: '县级', params: {data: [],options:{}}}
        ],
        djjgPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["58%", "78%"],
            center: ["50%", "50%"],
            centerRadius: ["45%", "46%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.year, this.curTypeId);
    },

    methods: {
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.year, this.curTypeId);
      },
      getAPI(year, id) {
        // 1.信访目的占比（饼图）
        get_http("zlfxXfmdZbPie", {year: year, xfxs: id})
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2信访目的走势
        this.getZsLineAPI(year, id, '年度');
        // 3四级登记机构-信访目的占比（饼图）
        this.getDjjgZbPieAPI(year, id);
      },
      // 信访形式走势
      getZsLineAPI(year, id, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("zlfxXfmdZsLine", {year: _year, timetype: timetype, xfxs: id})
          .then(res => {
            this.zsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 四级登记机构-信访形式占比（饼图）
      getDjjgZbPieAPI(year, id) {
        for (let item of this.djjgArr.values()) {
          item.params.options = this.djjgPieParams.options;
          get_http("zlfxXfmdJgPie", {year: year, djjg: item.name, xfxs: id})
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },

      // 信访目的走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, this.curTypeId, time);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box

      .c-top
        flex 1
        display flex

        .ct-left
          flex 1
          height 100%
          position relative
          border-right 1px #91e3f7 dashed
          font-size 0
          .inner
            display flex
            width 100%
            height calc(100% - 0.28rem)
            .chart
              width 100%
              height 100%
        .ct-right
          flex 1.7
          height 100%
          position relative

          .timeChange
            top 0
            right .9rem

          .inner
            width 100%
            height calc(100% - 0.28rem)
            box-sizing border-box

      .c-bottom
        flex 1
        background url('../../../assets/c-bottom-bg.png') no-repeat center
        background-size 100% 100%
        position relative
        box-sizing border-box
        padding .4rem .3rem 0 .3rem
        margin 0 .2rem .1rem .2rem
        .inner
          display flex
          width 100%
          height calc(100% - 0.7rem)
          .chart
            flex 1
            .label
              text-align center
              color #fff
              font-weight 700
              font-size 0.2rem
</style>
